import React, { useState, useCallback } from 'react';
import { UserFormData, FormErrors } from '../types';
import { validateForm, validateField, hasFormErrors } from '../utils/validation';
import { UserIcon, AcademicCapIcon, GlobeAltIcon, DocumentTextIcon, LanguageIcon } from '@heroicons/react/24/outline';

interface UserFormProps {
  formData: UserFormData;
  onFormChange: (data: UserFormData) => void;
  onSubmit: () => void;
  isGenerating: boolean;
  errors: FormErrors;
  onErrorsChange: (errors: FormErrors) => void;
}

const UserForm: React.FC<UserFormProps> = ({
  formData,
  onFormChange,
  onSubmit,
  isGenerating,
  errors,
  onErrorsChange
}) => {
  const [activeSection, setActiveSection] = useState<string>('personal');
  const [localErrors, setLocalErrors] = useState<FormErrors>({});

  // 
  const handleFieldChange = useCallback((field: keyof UserFormData, value: string) => {
    const newFormData = { ...formData, [field]: value };
    onFormChange(newFormData);

    // 
    const fieldError = validateField(field, value, formData);
    const newErrors = { ...localErrors };
    if (fieldError) {
      newErrors[field] = fieldError;
    } else {
      delete newErrors[field];
    }
    setLocalErrors(newErrors);
    onErrorsChange(newErrors);
  }, [formData, localErrors, onFormChange, onErrorsChange]);

  // 
  const handleSubmit = useCallback((e: React.FormEvent) => {
    e.preventDefault();
    const validationErrors = validateForm(formData);
    setLocalErrors(validationErrors);
    onErrorsChange(validationErrors);
    
    if (!hasFormErrors(validationErrors)) {
      onSubmit();
    } else {
      // 
      const firstErrorField = Object.keys(validationErrors)[0];
      const element = document.getElementById(firstErrorField);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth', block: 'center' });
        element.focus();
      }
    }
  }, [formData, onSubmit, onErrorsChange]);

  // 
  const sections = [
    { id: 'personal', title: '', icon: UserIcon },
    { id: 'education', title: '', icon: AcademicCapIcon },
    { id: 'application', title: '', icon: GlobeAltIcon },
    { id: 'experience', title: '', icon: DocumentTextIcon },
    { id: 'language', title: '', icon: LanguageIcon },
    { id: 'statement', title: '', icon: DocumentTextIcon }
  ];

  // 
  const renderInput = (
    field: keyof UserFormData,
    label: string,
    type: string = 'text',
    placeholder?: string,
    required: boolean = false
  ) => {
    const error = localErrors[field] || errors[field];
    return (
      <div className="mb-4">
        <label htmlFor={field} className="form-label">
          {label}
          {required && <span className="text-red-500 ml-1">*</span>}
        </label>
        <input
          id={field}
          type={type}
          value={formData[field]}
          onChange={(e) => handleFieldChange(field, e.target.value)}
          placeholder={placeholder}
          className={`form-input ${
            error ? 'border-red-500 focus:ring-red-500' : ''
          }`}
          required={required}
        />
        {error && (
          <p className="mt-1 text-sm text-red-600">{error}</p>
        )}
      </div>
    );
  };

  // 
  const renderSelect = (
    field: keyof UserFormData,
    label: string,
    options: { value: string; label: string }[],
    required: boolean = false
  ) => {
    const error = localErrors[field] || errors[field];
    return (
      <div className="mb-4">
        <label htmlFor={field} className="form-label">
          {label}
          {required && <span className="text-red-500 ml-1">*</span>}
        </label>
        <select
          id={field}
          value={formData[field]}
          onChange={(e) => handleFieldChange(field, e.target.value)}
          className={`form-input ${
            error ? 'border-red-500 focus:ring-red-500' : ''
          }`}
          required={required}
        >
          <option value="">...</option>
          {options.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </select>
        {error && (
          <p className="mt-1 text-sm text-red-600">{error}</p>
        )}
      </div>
    );
  };

  // 
  const renderTextarea = (
    field: keyof UserFormData,
    label: string,
    placeholder?: string,
    required: boolean = false,
    rows: number = 4
  ) => {
    const error = localErrors[field] || errors[field];
    return (
      <div className="mb-4">
        <label htmlFor={field} className="form-label">
          {label}
          {required && <span className="text-red-500 ml-1">*</span>}
        </label>
        <textarea
          id={field}
          value={formData[field]}
          onChange={(e) => handleFieldChange(field, e.target.value)}
          placeholder={placeholder}
          rows={rows}
          className={`form-input resize-none ${
            error ? 'border-red-500 focus:ring-red-500' : ''
          }`}
          required={required}
        />
        <div className="flex justify-between mt-1">
          {error && (
            <p className="text-sm text-red-600">{error}</p>
          )}
          <p className="text-sm text-gray-500 ml-auto">
            {formData[field].length} 
          </p>
        </div>
      </div>
    );
  };

  return (
    <div className="h-full flex flex-col">
      {/*  */}
      <div className="flex-shrink-0 border-b border-gray-200 bg-white overflow-hidden">
        <nav className="nav-tabs-container space-x-1 p-4 scrollbar-hide w-full">
          {sections.map((section) => {
            const Icon = section.icon;
            return (
              <button
                key={section.id}
                type="button"
                onClick={() => setActiveSection(section.id)}
                className={`nav-tab-button flex items-center px-3 py-2 rounded-lg text-sm font-medium whitespace-nowrap transition-colors ${
                  activeSection === section.id
                    ? 'bg-primary-100 text-primary-700'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-100'
                }`}
              >
                <Icon className="w-4 h-4 mr-2" />
                {section.title}
              </button>
            );
          })}
        </nav>
      </div>

      {/*  */}
      <div className="flex-1 overflow-y-auto p-6">
        <form onSubmit={handleSubmit} className="space-y-6">
          {/*  */}
          {activeSection === 'personal' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
              {renderInput('fullName', '', 'text', '', true)}
              {renderInput('email', '', 'email', 'your@email.com', true)}
              {renderInput('phone', '', 'tel', '')}
              {renderInput('dateOfBirth', '', 'date')}
              {renderInput('nationality', '', 'text', '', true)}
            </div>
          )}

          {/*  */}
          {activeSection === 'education' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
              {renderSelect('currentEducation', '', [
                { value: 'high-school', label: '' },
                { value: 'undergraduate', label: '' },
                { value: 'graduate', label: '' },
                { value: 'phd', label: '' }
              ], true)}
              {renderInput('institution', '', 'text', '', true)}
              {renderInput('major', '', 'text', '', true)}
              {renderInput('gpa', 'GPA', 'text', 'GPA0-4.0')}
              {renderInput('graduationDate', '', 'date')}
            </div>
          )}

          {/*  */}
          {activeSection === 'application' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
              {renderSelect('targetCountry', '', [
                { value: 'usa', label: '' },
                { value: 'uk', label: '' },
                { value: 'canada', label: '' },
                { value: 'australia', label: '' },
                { value: 'germany', label: '' },
                { value: 'france', label: '' },
                { value: 'singapore', label: '' },
                { value: 'other', label: '' }
              ], true)}
              {renderInput('targetUniversity', '', 'text', '', true)}
              {renderInput('targetMajor', '', 'text', '', true)}
              {renderInput('applicationDeadline', '', 'date')}
            </div>
          )}

          {/*  */}
          {activeSection === 'experience' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
              {renderTextarea('workExperience', '', '...')}
              {renderTextarea('researchExperience', '', '...')}
              {renderTextarea('volunteerExperience', '', '...')}
              {renderTextarea('awards', '', '...')}
            </div>
          )}

          {/*  */}
          {activeSection === 'language' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
              {renderSelect('englishTest', '', [
                { value: 'toefl', label: 'TOEFL' },
                { value: 'ielts', label: 'IELTS' },
                { value: 'gre', label: 'GRE' },
                { value: 'gmat', label: 'GMAT' },
                { value: 'other', label: '' }
              ])}
              {renderInput('englishScore', '', 'text', '')}
              {renderTextarea('otherLanguages', '', '...')}
            </div>
          )}

          {/*  */}
          {activeSection === 'statement' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
              {renderTextarea('careerGoals', '', '...', true, 6)}
              {renderTextarea('whyThisMajor', '', '...', true, 6)}
              {renderTextarea('whyThisUniversity', '', '...', true, 6)}
              {renderTextarea('personalChallenges', '', '...', false, 6)}
              {renderTextarea('uniqueQualities', '', '...', false, 6)}
              {renderTextarea('additionalInfo', '', '...', false, 4)}
            </div>
          )}
        </form>
      </div>

      {/*  */}
      <div className="flex-shrink-0 border-t border-gray-200 bg-white p-6">
        <button
          type="button"
          onClick={handleSubmit}
          disabled={isGenerating}
          className={`w-full btn-primary ${
            isGenerating ? 'opacity-50 cursor-not-allowed' : ''
          }`}
        >
          {isGenerating ? (
            <div className="flex items-center justify-center">
              <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
              ...
            </div>
          ) : (
            ''
          )}
        </button>
        
        {hasFormErrors(localErrors) && (
          <p className="mt-2 text-sm text-red-600 text-center">
            
          </p>
        )}
      </div>
    </div>
  );
};

export default UserForm;