import React, { useState, useCallback, useEffect } from 'react';
import UserForm from './components/UserForm';
import DocumentPreview from './components/DocumentPreview';
import { UserFormData, FormErrors, GenerationStatus, StreamData, DownloadOptions } from './types';
import { generateDocument } from './services/api';
import { downloadDocument } from './utils/download';
import { validateForm, hasFormErrors } from './utils/validation';
import { AcademicCapIcon, ExclamationTriangleIcon } from '@heroicons/react/24/outline';

// 
const initialFormData: UserFormData = {
  fullName: '',
  email: '',
  phone: '',
  dateOfBirth: '',
  nationality: '',
  currentEducation: '',
  institution: '',
  major: '',
  gpa: '',
  graduationDate: '',
  targetCountry: '',
  targetUniversity: '',
  targetMajor: '',
  applicationDeadline: '',
  workExperience: '',
  researchExperience: '',
  volunteerExperience: '',
  awards: '',
  englishTest: '',
  englishScore: '',
  otherLanguages: '',
  careerGoals: '',
  whyThisMajor: '',
  whyThisUniversity: '',
  personalChallenges: '',
  uniqueQualities: '',
  additionalInfo: ''
};

const App: React.FC = () => {
  // 
  const [formData, setFormData] = useState<UserFormData>(initialFormData);
  const [generatedContent, setGeneratedContent] = useState<string>('');
  const [generationStatus, setGenerationStatus] = useState<GenerationStatus>('idle');
  const [isStreaming, setIsStreaming] = useState<boolean>(false);
  const [errors, setErrors] = useState<FormErrors>({});
  const [documentType] = useState<string>('personal-statement');
  const [connectionError, setConnectionError] = useState<string>('');

  // localStorage
  useEffect(() => {
    const savedData = localStorage.getItem('study-abroad-form-data');
    if (savedData) {
      try {
        const parsedData = JSON.parse(savedData);
        setFormData({ ...initialFormData, ...parsedData });
      } catch (error) {
        console.error(':', error);
      }
    }
  }, []);

  // localStorage
  useEffect(() => {
    const timeoutId = setTimeout(() => {
      localStorage.setItem('study-abroad-form-data', JSON.stringify(formData));
    }, 1000); // 1

    return () => clearTimeout(timeoutId);
  }, [formData]);

  // 
  const handleFormChange = useCallback((newFormData: UserFormData) => {
    setFormData(newFormData);
  }, []);

  // 
  const handleErrorsChange = useCallback((newErrors: FormErrors) => {
    setErrors(newErrors);
  }, []);

  // 
  const handleStreamData = useCallback((data: StreamData) => {
    if (data.error) {
      setGenerationStatus('error');
      setIsStreaming(false);
      setConnectionError(data.error);
      return;
    }

    if (data.content) {
      setGeneratedContent(prev => prev + data.content);
    }

    if (data.isComplete) {
      setGenerationStatus('completed');
      setIsStreaming(false);
    }
  }, []);

  // 
  const handleGenerateDocument = useCallback(async () => {
    // 
    const validationErrors = validateForm(formData);
    setErrors(validationErrors);
    
    if (hasFormErrors(validationErrors)) {
      alert('');
      return;
    }

    // 
    setGenerationStatus('generating');
    setIsStreaming(true);
    setGeneratedContent('');
    setConnectionError('');

    try {
      await generateDocument(formData, documentType, handleStreamData);
    } catch (error) {
      console.error(':', error);
      setGenerationStatus('error');
      setIsStreaming(false);
      setConnectionError(error instanceof Error ? error.message : '');
    }
  }, [formData, documentType, handleStreamData]);

  // 
  const handleDownload = useCallback(async (options: DownloadOptions) => {
    try {
      await downloadDocument(generatedContent, options);
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  }, [generatedContent]);

  // 
  const handleClearForm = useCallback(() => {
    if (window.confirm('')) {
      setFormData(initialFormData);
      setGeneratedContent('');
      setGenerationStatus('idle');
      setErrors({});
      localStorage.removeItem('study-abroad-form-data');
    }
  }, []);

  // 
  const handleRegenerate = useCallback(() => {
    if (window.confirm('')) {
      handleGenerateDocument();
    }
  }, [handleGenerateDocument]);

  return (
    <div className="min-h-screen bg-gray-50">
      {/*  */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center">
              <AcademicCapIcon className="w-8 h-8 text-primary-600 mr-3" />
              <div>
                <h1 className="text-xl font-bold text-gray-900"></h1>
                <p className="text-sm text-gray-500">AI</p>
              </div>
            </div>
            
            <div className="flex items-center space-x-4">
              {generatedContent && (
                <button
                  type="button"
                  onClick={handleRegenerate}
                  className="btn-secondary text-sm"
                  disabled={generationStatus === 'generating'}
                >
                  
                </button>
              )}
              
              <button
                type="button"
                onClick={handleClearForm}
                className="text-sm text-gray-600 hover:text-gray-900 transition-colors"
              >
                
              </button>
            </div>
          </div>
        </div>
      </header>

      {/*  */}
      {connectionError && (
        <div className="bg-red-50 border-l-4 border-red-400 p-4 mx-4 mt-4 rounded-r-lg">
          <div className="flex">
            <ExclamationTriangleIcon className="h-5 w-5 text-red-400 mr-2" />
            <div>
              <h3 className="text-sm font-medium text-red-800"></h3>
              <p className="text-sm text-red-700 mt-1">{connectionError}</p>
              <p className="text-xs text-red-600 mt-2">
                
              </p>
            </div>
          </div>
        </div>
      )}

      {/*  */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 h-[calc(100vh-140px)]">
          {/*  */}
          <div className="card overflow-hidden">
            <UserForm
              formData={formData}
              onFormChange={handleFormChange}
              onSubmit={handleGenerateDocument}
              isGenerating={generationStatus === 'generating'}
              errors={errors}
              onErrorsChange={handleErrorsChange}
            />
          </div>

          {/*  */}
          <div className="card overflow-hidden">
            <DocumentPreview
              content={generatedContent}
              isGenerating={generationStatus === 'generating'}
              isStreaming={isStreaming}
              documentType={documentType}
              onDownload={handleDownload}
            />
          </div>
        </div>
      </main>

      {/*  */}
      <footer className="bg-white border-t border-gray-200 mt-8">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between text-sm text-gray-500">
            <div className="flex items-center space-x-4">
              <span> 2024 </span>
              <span></span>
              <span>AI</span>
            </div>
            
            <div className="flex items-center space-x-4">
              <span className="flex items-center">
                : 
                <span className={`ml-1 inline-block w-2 h-2 rounded-full ${
                  generationStatus === 'generating' ? 'bg-yellow-400 animate-pulse' :
                  generationStatus === 'completed' ? 'bg-green-400' :
                  generationStatus === 'error' ? 'bg-red-400' :
                  'bg-gray-400'
                }`}></span>
                <span className="ml-1">
                  {generationStatus === 'generating' ? '' :
                   generationStatus === 'completed' ? '' :
                   generationStatus === 'error' ? '' :
                   ''}
                </span>
              </span>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default App;