import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import { DownloadOptions } from '../types';

// PDF
export const downloadAsPDF = async (
  content: string,
  fileName: string = 'document.pdf'
): Promise<void> => {
  try {
    // DOM
    const tempDiv = document.createElement('div');
    tempDiv.innerHTML = content;
    tempDiv.style.cssText = `
      position: absolute;
      top: -9999px;
      left: -9999px;
      width: 800px;
      padding: 40px;
      font-family: 'Times New Roman', serif;
      font-size: 12px;
      line-height: 1.6;
      color: #000;
      background: #fff;
    `;
    
    document.body.appendChild(tempDiv);

    // html2canvas
    const canvas = await html2canvas(tempDiv, {
      scale: 2,
      useCORS: true,
      allowTaint: true,
      backgroundColor: '#ffffff'
    });

    // 
    document.body.removeChild(tempDiv);

    // PDF
    const pdf = new jsPDF({
      orientation: 'portrait',
      unit: 'mm',
      format: 'a4'
    });

    const imgData = canvas.toDataURL('image/png');
    const imgWidth = 210; // A4
    const pageHeight = 295; // A4
    const imgHeight = (canvas.height * imgWidth) / canvas.width;
    let heightLeft = imgHeight;
    let position = 0;

    // 
    pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
    heightLeft -= pageHeight;

    // 
    while (heightLeft >= 0) {
      position = heightLeft - imgHeight;
      pdf.addPage();
      pdf.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
      heightLeft -= pageHeight;
    }

    // PDF
    pdf.save(fileName);
  } catch (error) {
    console.error('PDF:', error);
    throw new Error('PDF');
  }
};

// WordRTF
export const downloadAsWord = (
  content: string,
  fileName: string = 'document.docx'
): void => {
  try {
    // HTMLRTF
    const rtfContent = convertHtmlToRtf(content);
    
    const blob = new Blob([rtfContent], {
      type: 'application/rtf'
    });
    
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = fileName.replace('.docx', '.rtf');
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  } catch (error) {
    console.error('Word:', error);
    throw new Error('Word');
  }
};

// 
export const downloadAsText = (
  content: string,
  fileName: string = 'document.txt'
): void => {
  try {
    // HTML
    const textContent = content
      .replace(/<[^>]*>/g, '')
      .replace(/&nbsp;/g, ' ')
      .replace(/&amp;/g, '&')
      .replace(/&lt;/g, '<')
      .replace(/&gt;/g, '>')
      .replace(/&quot;/g, '"')
      .trim();
    
    const blob = new Blob([textContent], {
      type: 'text/plain;charset=utf-8'
    });
    
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = fileName;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  } catch (error) {
    console.error(':', error);
    throw new Error('');
  }
};

// HTMLRTF
const convertHtmlToRtf = (html: string): string => {
  const rtfHeader = `{\\rtf1\\ansi\\deff0 {\\fonttbl {\\f0 Times New Roman;}}\\f0\\fs24`;
  const rtfFooter = '}';
  
  let rtfContent = html
    // HTML
    .replace(/<h1[^>]*>(.*?)<\/h1>/gi, '\\b $1\\b0\\par\\par')
    .replace(/<h2[^>]*>(.*?)<\/h2>/gi, '\\b $1\\b0\\par')
    .replace(/<h3[^>]*>(.*?)<\/h3>/gi, '\\b $1\\b0\\par')
    .replace(/<p[^>]*>(.*?)<\/p>/gi, '$1\\par')
    .replace(/<br\s*\/?>/gi, '\\par')
    .replace(/<strong[^>]*>(.*?)<\/strong>/gi, '\\b $1\\b0')
    .replace(/<b[^>]*>(.*?)<\/b>/gi, '\\b $1\\b0')
    .replace(/<em[^>]*>(.*?)<\/em>/gi, '\\i $1\\i0')
    .replace(/<i[^>]*>(.*?)<\/i>/gi, '\\i $1\\i0')
    .replace(/<ul[^>]*>(.*?)<\/ul>/gi, '$1')
    .replace(/<ol[^>]*>(.*?)<\/ol>/gi, '$1')
    .replace(/<li[^>]*>(.*?)<\/li>/gi, ' $1\\par')
    .replace(/<[^>]*>/g, '') // HTML
    .replace(/&nbsp;/g, ' ')
    .replace(/&amp;/g, '&')
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&quot;/g, '"');
  
  return rtfHeader + rtfContent + rtfFooter;
};

// 
export const downloadDocument = async (
  content: string,
  options: DownloadOptions
): Promise<void> => {
  const { format, fileName } = options;
  
  switch (format) {
    case 'pdf':
      await downloadAsPDF(content, fileName);
      break;
    case 'docx':
      downloadAsWord(content, fileName);
      break;
    case 'txt':
      downloadAsText(content, fileName);
      break;
    default:
      throw new Error(`: ${format}`);
  }
};

// 
export const generateFileName = (
  documentType: string,
  format: string,
  timestamp?: Date
): string => {
  const date = timestamp || new Date();
  const dateStr = date.toISOString().split('T')[0];
  const typeMap: { [key: string]: string } = {
    'personal-statement': '',
    'motivation-letter': '',
    'research-proposal': '',
    'recommendation-letter': ''
  };
  
  const typeName = typeMap[documentType] || documentType;
  return `${typeName}_${dateStr}.${format}`;
};